import React, { Component} from "react";
import * as THREE from "three";
import * as dat from 'lil-gui'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js'
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js'
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js'
import { DotScreenPass } from 'three/examples/jsm/postprocessing/DotScreenPass.js'
import { GlitchPass } from 'three/examples/jsm/postprocessing/GlitchPass.js'
import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass.js'
import { RGBShiftShader } from 'three/examples/jsm/shaders/RGBShiftShader.js'
import { GammaCorrectionShader } from 'three/examples/jsm/shaders/GammaCorrectionShader.js'
import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass.js'

import maybe from './assets/Studios_Chain2.glb'

// Import CSS
import './WebGL.css';

class WebGL extends Component {	

	componentDidMount() {
		
		// Canvas
		const canvas = document.querySelector('canvas.webgl')

		const sizes = {
			width: window.innerWidth,
			height: window.innerHeight
		}
		//------------------------------------------------------------------------------------------------
		//**                                        RENDERER
		//------------------------------------------------------------------------------------------------
		
		/**
		 * Renderer
		 */
		 const renderer = new THREE.WebGLRenderer({
			canvas: canvas,
			antialias: true
		})
		renderer.setSize(sizes.width, sizes.height)
		renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
		renderer.outputEncoding = THREE.sRGBEncoding

		const gui = new dat.GUI({
			width: 400
		})
		gui.hide()
		window.addEventListener('keydown', (event) =>
		{
			if(event.key === 'h')
			{
					if(gui._hidden)
						gui.show()
					else
						gui.hide()
				}
		})
		
		const datParameters = {
			color: 0xbf4808
		}
		
		// Scene
		const scene = new THREE.Scene()
		
		/**
		 * Loaders
		 */
		
		// Draco loader
		const dracoLoader = new DRACOLoader()
		dracoLoader.setDecoderPath('./static/draco/')
		
		// GLTF loader
		const gltfLoader = new GLTFLoader()
		gltfLoader.setDRACOLoader(dracoLoader)
		
		//Loading Manager
		const loadingManager = new THREE.LoadingManager()
		loadingManager.onStart = () =>
		{
			//console.log('onStart')
		}
		loadingManager.onLoad = () =>
		{
			//console.log('onLoad')
		}
		loadingManager.onProgress = () =>
		{
			//console.log('onProgress')
		}
		loadingManager.onError = () =>
		{
			//console.log('onError')
		}
		
		// Texture loader
		const textureLoader = new THREE.TextureLoader(loadingManager)
		
		//------------------------------------------------------------------------------------------------
		//**                                        TEXTURES & MATS
		//------------------------------------------------------------------------------------------------
		
		//Materials		
		const cardColor = textureLoader.load('./static/Materials/Card Combine 2.png')
		cardColor.flipY = false
		cardColor.encoding = THREE.sRGBEncoding
		const cardRough = textureLoader.load('./static/Materials/Card Combine 2.png')
		cardRough.flipY = false
		cardRough.encoding = THREE.sRGBEncoding
		const cardMat = new THREE.MeshBasicMaterial({ map: cardColor })		
		cardMat.map = cardColor
		cardMat.roughnessMap = cardColor
		//------------------------------------------------------------------------------------------------
		//**                                        LIGHTING
		//------------------------------------------------------------------------------------------------
		// // Ambient
		const pointLight1 = new THREE.PointLight()
		pointLight1.color = new THREE.Color(0xffffff)
		pointLight1.intensity = .5
		pointLight1.position.z = - 8
		pointLight1.position.y = 8
		pointLight1.position.x = - 8
		scene.add(pointLight1)		
		const pointLight2 = new THREE.PointLight()
		pointLight2.color = new THREE.Color(0xffffff)
		pointLight2.intensity = 1
		pointLight2.position.z = - 2
		pointLight2.position.y = 2
		pointLight2.position.x = - 2
		scene.add(pointLight2)	
		//------------------------------------------------------------------------------------------------
		//**                                        COMPONENTS
		//------------------------------------------------------------------------------------------------
				/**
		 * Model
		 */
		
		 let mixer = null
		 
		
		 gltfLoader.load(maybe,
			(gltf) =>
			{
				//console.log(gltf.scene)
				const cardMesh = gltf.scene.children.find(child => child => child.name === 'card')				
				cardMesh.material = cardMat	
				gltf.scene.scale.set(1, 1, 1)
				gltf.scene.rotation.set(-0, 0, 0)
				scene.add(gltf.scene)
				mixer = new THREE.AnimationMixer(gltf.scene)
				const action = mixer.clipAction(gltf.animations[0])
				const action2 = mixer.clipAction(gltf.animations[1])
				const action3 = mixer.clipAction(gltf.animations[2])
				action.play()
				action2.play()
				action3.play()
			}
		)		
		/**
		 * Particles
		 */
		// Geometry
		const objectsDistance = 20
		const particlesCount = 2000		
		const positions = new Float32Array(particlesCount * 3)
		
		for(let i = 0; i < particlesCount; i++)
		{
			positions[i * 3 + 0] = (Math.random() - 0.5) * 100
			positions[i * 3 + 1] = objectsDistance * 0.5 - Math.random() * objectsDistance 
			positions[i * 3 + 2] = (Math.random() - 0.5) * 100
		}
		
		const particlesGeometry = new THREE.BufferGeometry()
		particlesGeometry.setAttribute('position', new THREE.BufferAttribute(positions, 3))
		
		// Material
		const particlesMaterial = new THREE.PointsMaterial({
			color: 0xfcfe86,
			sizeAttenuation: textureLoader,
			size: 0.03
		})
		
		// Points
		const particles = new THREE.Points(particlesGeometry, particlesMaterial)
		scene.add(particles)
		const objectsDistance2 = 120
		const particlesCount2 = 400		
		const positions2 = new Float32Array(particlesCount2 * 3)
		
		for(let i = 0; i < particlesCount2; i++)
		{
			positions2[i * 3 + 0] = (Math.random() - 0.5) * 50
			positions2[i * 3 + 1] = objectsDistance2 * 0.5 - Math.random() * objectsDistance2 
			positions2[i * 3 + 2] = (Math.random() - 0.5) * 25
		}
		
		const particlesGeometry2 = new THREE.BufferGeometry()
		particlesGeometry2.setAttribute('position', new THREE.BufferAttribute(positions2, 3))
		
		// Material
		const particlesMaterial2 = new THREE.PointsMaterial({
			color: 0xfcfe86,
			sizeAttenuation: textureLoader,
			size: 0.03
		})
		
		const particles2 = new THREE.Points(particlesGeometry2, particlesMaterial2)
		scene.add(particles2)
		
		//------------------------------------------------------------------------------------------------
		//**                                        SIZES & RESIZE
		//------------------------------------------------------------------------------------------------
		/**
		 * Sizes
		 */
		
		
		window.addEventListener('resize', () =>
		{
			// Update sizes
			sizes.width = window.innerWidth
			sizes.height = window.innerHeight
		
			// Update camera
			camera.aspect = sizes.width / sizes.height
			camera.updateProjectionMatrix()
		
			// Update renderer
			renderer.setSize(sizes.width, sizes.height)
			renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
		})
		
		//------------------------------------------------------------------------------------------------
		//**                                        CAMERA & CONTROLS
		//------------------------------------------------------------------------------------------------
		
		/**
		 * Camera
		 */
		// Base camera
		const camera = new THREE.PerspectiveCamera(45, sizes.width / sizes.height, 0.1, 100)
		camera.position.x = 0
		camera.position.y = 1
		camera.position.z = 5
		scene.add(camera)
		
		// Controls
		
		const controls = new OrbitControls(camera, canvas)
		controls.enableDamping = true
		
		//Scroll and Cursor
		
		let scrollY = window.scrollY
		let currentSection = 0
		
		window.addEventListener('scroll', () =>
		{
			scrollY = window.scrollY
		
		
		})
		
		const cursor = {}
		cursor.x = 0
		cursor.y = 0
		
		window.addEventListener('mousemove', (event) =>
		{
			cursor.x = event.clientX / sizes.width - 0.5
			cursor.y = event.clientY / sizes.height - 0.5
		})
		
		//------------------------------------------------------------------------------------------------
		//**                                        DAT.GUI
		//------------------------------------------------------------------------------------------------
		
		gui.addColor(datParameters, 'color').name('Particle Color')
			.onChange(() =>
			{
				particlesMaterial.color.set(datParameters.color)
			})
		
		gui.add(particlesMaterial, 'size').min(0).max(1).step(0.01).name('Particle Size')
		
		//------------------------------------------------------------------------------------------------
		//**                                        Post Processing
		//------------------------------------------------------------------------------------------------
		
		
		/**
		 * Post processing
		 */
		 const effectComposer = new EffectComposer(renderer)
		 effectComposer.setSize(sizes.width, sizes.height)
		 effectComposer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
		
		 
		const renderPass = new RenderPass(scene, camera)
		effectComposer.addPass(renderPass)
		
		//----------
		
		const dotScreenPass = new DotScreenPass()
		dotScreenPass.enabled = false
		effectComposer.addPass(dotScreenPass)
		
		const glitchPass = new GlitchPass()
		glitchPass.goWild = false
		glitchPass.enabled = false
		glitchPass.strength = 0.3
		glitchPass.scale = 1
		glitchPass.distortion_x = 1
		glitchPass.randX = 1
		effectComposer.addPass(glitchPass)
		
		const rgbShiftPass = new ShaderPass(RGBShiftShader)
		rgbShiftPass.enabled = false
		rgbShiftPass.scale = 2
		effectComposer.addPass(rgbShiftPass)
		
		const gammaCorrectionPass = new ShaderPass(GammaCorrectionShader)
		gammaCorrectionPass.enabled = false
		effectComposer.addPass(gammaCorrectionPass)
		
		const unrealBloomPass = new UnrealBloomPass()
		unrealBloomPass.strength = 0.54
		unrealBloomPass.radius = .22
		unrealBloomPass.threshold = 0.88
		unrealBloomPass.enabled = false
		effectComposer.addPass(unrealBloomPass)
		
		//anti aliasing
		
		let RenderTargetClass = null
		
		if(renderer.getPixelRatio() === 1 && renderer.capabilities.isWebGL2)
		{
			RenderTargetClass = THREE.WebGLMultisampleRenderTarget
			//console.log('Using WebGLMultisampleRenderTarget')
		}
		else
		{
			RenderTargetClass = THREE.WebGLRenderTarget
			//console.log('Using WebGLRenderTarget')
		}
		
		const renderTarget = new RenderTargetClass(
			// ...
		)
		if(renderer.getPixelRatio() === 1 && !renderer.capabilities.isWebGL2)
		{
			//const smaaPass = new SMAAPass()
			//effectComposer.addPass(smaaPass)
		
			//console.log('Using SMAA')
		}
		
		
		//------------------------------------------------------------------------------------------------
		//**                                        Effects GUI
		//------------------------------------------------------------------------------------------------
		
		const folderEffects = gui.addFolder('Post Prod Effects');
		const folderBloom = folderEffects.addFolder('Bloom');
		folderBloom.add(unrealBloomPass, 'enabled')
		folderBloom.add(unrealBloomPass, 'strength').min(0).max(1).step(0.01).name('Bloom Strength')
		folderBloom.add(unrealBloomPass, 'radius').min(0).max(1).step(0.01).name('Bloom Radius')
		folderBloom.add(unrealBloomPass, 'threshold').min(0).max(1).step(0.01).name('Bloom threshold')
		
		const folderGlitch = folderEffects.addFolder('Glitch');
		folderGlitch.add(glitchPass, 'enabled').name('Enable')
		folderGlitch.add(glitchPass, 'strength').min(0).max(12).step(0.01).name(' Strength')
		folderGlitch.add(glitchPass, 'distortion_x').min(0).max(12).step(0.01).name(' distortion_x')
		folderGlitch.add(glitchPass, 'randX').min(0).max(99).step(0.01).name('Glitch Time Displace')
		folderGlitch.add(glitchPass, 'scale').min(0).max(12).step(0.01).name('Glitch Time Displace')
		
		const folderRGB = folderEffects.addFolder('RGB Shift');
		folderRGB.add(rgbShiftPass, 'enabled').name('Enable')
		
		const folderDot = folderEffects.addFolder('Dot Screen');
		folderDot.add(dotScreenPass, 'enabled').name('Enable')
		
		const folderGamma = folderEffects.addFolder('Gamma Correction');
		folderGamma.add(gammaCorrectionPass, 'enabled').name('Enable')
		//------------------------------------------------------------------------------------------------
		//**                                        ANIMATE
		//------------------------------------------------------------------------------------------------
		
		
		/**
		 * Animate
		 */
		const clock = new THREE.Clock()
		let previousTime = 0
		
		const tick = () =>
		{
			const elapsedTime = clock.getElapsedTime()
			const deltaTime = elapsedTime - previousTime
			previousTime = elapsedTime
		
			//** -------------------------        Update controls       ---------------------------- **// 
			//**             Animate Camera on Mouse move Parralax
			const parallaxX = cursor.x * 0.5
			const parallaxY = - cursor.y * 0.5
			camera.position.x += (parallaxX ) * 1 * deltaTime   
			camera.position.y += (parallaxY ) * 1 * deltaTime
		
			
			
		
			  //**              Update Controls for Orbital                      */
			  controls.update()
			  
			  //**              Update Mixer GLTF Animation                      */
			  if(mixer)
			{
				mixer.update(deltaTime)
			}		
		
			// Render
			renderer.render(scene, camera)
		
			//effects Render
			effectComposer.render()
		
			// Call tick again on the next frame
			window.requestAnimationFrame(tick)
		}
		
		tick()

	}
	render() {	  
	  return (		
		<div></div>		
	  );	
	}
}
export default WebGL;